'use strict';

/**
* Convert an integer to a Slice object.
*
* @module @stdlib/slice-base-int2slice
*
* @example
* var int2slice = require( '@stdlib/slice-base-int2slice' );
*
* var s = int2slice( -4, 10, false );
* // returns <Slice>
*
* var start = s.start;
* // returns 6
*
* var stop = s.stop;
* // returns 7
*
* var step = s.step;
* // returns 1
*/

// MODULES //

var main = require( './main.js' );


// EXPORTS //

module.exports = main;
